import { Typography } from "@mui/material";
import Button from "@mui/material/Button";
import "../App.css";
import { useEffect, useState } from "react";
import axios from "axios";
import moment from "moment";
import { useTranslation } from "react-i18next";
import "moment/min/locales";

let cancelToken = null;
moment.locale("en");
export default function WeatherAppDetails() {
  const { t, i18n } = useTranslation();
  let [locale, setLocale] = useState("en");
  let [weatherStatus, setWeatherStatus] = useState({});
  let [name, setName] = useState();
  let [temp, setTemp] = useState({});
  let [date, setDate] = useState("");
  /*
weekday: 'long': This gives the full name of the day (e.g., "Monday").
month: 'numeric': This gives the numeric representation of the month (e.g., "1" for January).
day: 'numeric': This gives the day of the month (e.g., "27").
year: 'numeric': This gives the year (e.g., "2025").
 */
  useEffect(() => {
    let longLat = { lat: 29.93, lon: 30.94 };
    axios
      .get(
        `https://api.openweathermap.org/data/2.5/weather?lat=${longLat.lat}&lon=${longLat.lon}&appid=aad9319522f19b3d2855df285e337a3f`,
        {
          cancelToken: new axios.CancelToken((c) => {
            cancelToken = c;
          }),
        }
      )
      .then(function (response) {
        setDate(moment().format("dddd l"));
        setName(response.data.name.replace("Governorate", ""));
        setWeatherStatus({
          description: response.data.weather[0].description,
          icon: `https://openweathermap.org/img/wn/${response.data.weather[0].icon}@2x.png`,
        });
        setTemp({
          temp: parseFloat((response.data.main.temp - 273.15).toFixed(1)),
          temp_min: parseFloat(
            (response.data.main.temp_min - 273.15).toFixed(1)
          ),
          temp_max: parseFloat(
            (response.data.main.temp_max - 273.15).toFixed(1)
          ),
        });
      })
      .catch(function (error) {});
    //callBack function
    return () => {
      cancelToken();
    };
  }, []);

  useEffect(() => {
    moment.locale(locale);
    setDate(moment().format("dddd l"));
    //lazem tstd3y setData tany 34an tktb 2l taree5 bs 2l mara deeh bel lo8a 2l tanya
    i18n.changeLanguage(locale);
  }, [locale]);

  function handleLocaleChange() {
    setLocale(locale === "en" ? "ar" : "en");
  }

  return (
    <Typography>
      <div
        style={{
          color: "white",
          border: "2px solid white",
          borderRadius: "10px",
          width: "300px",
          backgroundImage:
            "linear-gradient(to bottom right, #29b2dd 0%,  #33aadd 47%,  #2dc8ea 100%)",
        }}
      >
        <div
          style={{
            display: "flex",
            alignItems: "end",
            gap: "8px",
            margin: "20px 20px 10px 20px",
          }}
          dir={locale == "ar" ? "rtl" : "ltr"}
        >
          <h2 style={{ fontSize: "50px", lineHeight: "40px" }}>{t(name)}</h2>
          <p style={{ margin: "0px 10px" }}>{date}</p>
        </div>
        <hr style={{ border: "1px solid white" }} />
        <div
          style={{
            margin: "10px 10px 10px 15px",
            display: "flex",
            justifyContent: "space-around",
            alignItems: "center",
          }}
          dir={locale == "ar" ? "rtl" : "ltr"}
        >
          <div style={{ textAlign: "center" }}>
            <h3 style={{ fontSize: "60px" }}>
              {temp.temp}
              <sup style={{ fontSize: "20px" }}>C</sup>
            </h3>
            <p>{t(weatherStatus.description)}</p>
            <p>
              {t("min")} : {temp.temp_min} | {t("max")} : {temp.temp_max}
            </p>
          </div>
          <div>
            <img src={weatherStatus.icon} style={{ width: "120px" }} alt="" />
          </div>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "end",
            margin: "25px 30px",
          }}
          dir={locale == "ar" ? "rtl" : "ltr"}
        >
          <Button
            color="secondary"
            variant="contained"
            onClick={handleLocaleChange}
          >
            <Typography>{t("")}</Typography>
          </Button>
        </div>
      </div>
    </Typography>
  );
}
